<template>
  <div id="app">
    <div class="container-fluid px-0 pb-0">
      <div class="d-flex flex-column w-100 mx-auto px-0">
          <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <a class="navbar-brand">
              <h1>Ali<font style="color: rgb(201, 36, 17)">expert</font><sup><span class="badge badge-pill badge-danger">β</span></sup></h1>
            </a>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">

              <ul class="navbar-nav mr-auto">
                <li @click="setState('about')" class="nav-item" v-bind:class="{active: aboutFlag}">
                  <router-link class="nav-link"  v-bind:to="'/about'">О НАС</router-link>
                  <!-- <a class="nav-link" href="#">О НАС</a> -->
                </li>
                <li @click="setState('store')" class="nav-item" v-bind:class="{active: storeFlag}">
                  <router-link class="nav-link" v-bind:to="'/'">ТОВАРЫ</router-link>
                  <!-- <a class="nav-link" href="#">ТОВАРЫ<span class="sr-only">(current)</span></a> -->
                </li>
                <li @click="setState('feedback')" class="nav-item" v-bind:class="{active: feedbackFlag}">
                  <router-link class="nav-link" v-bind:to="'/feedback'">ОБРАТНАЯ СВЯЗЬ</router-link>
                </li>
              </ul>

              

              <div class="col-sm-3 px-0">
                <form class="form-inline input-group my-2 my-lg-0">
                  <input class="form-control" type="search" v-model="searchString" placeholder="Ключевые слова" aria-label="Search">
                  <div class="input-group-append">
                    <button class="btn btn-outline-danger" type="button">Поиск</button>
                  </div>
                </form>
              </div>

            </div>
          </nav>
          
      </div>
    </div>
  <router-view></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',
  props: {
    
  },
  data () {
    return {
      searchString: '',
      storeFlag: true,
      feedbackFlag: false,
      aboutFlag: false
    }
  },
  methods: {
    setState(state) {
      // console.log(window.location.href.endsWith("feedback"));
      switch (state) {
        case 'store':
          this.storeFlag = true;
          this.feedbackFlag = false;
          this.aboutFlag = false;
          break;
        case 'feedback':
          this.storeFlag = false;
          this.feedbackFlag = true;
          this.aboutFlag = false;
          break;
        case 'about':
          this.storeFlag = false;
          this.feedbackFlag = false;
          this.aboutFlag = true;
          break;
        default:
          this.storeFlag = true;
          this.feedbackFlag = false;
          this.aboutFlag = false;
          break;
      }
    }
  },
  computed: {
    
  }
}
</script>

<style scoped>

@font-face {
  font-family: 'PT Sans Narrow';
  src: url('./assets/fonts/PT_Sans-Narrow-Web-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'PT Sans Narrow Bold';
  src: url('./assets/fonts/PT_Sans-Narrow-Web-Bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
#app {
  font-family: 'Avenir', 'PT Sans Narrow', 'PT Sans Narrow Bold', Helvetica, Arial, sans-serif;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  text-align: left;
  color: #161d25;
  /* color: #2c3e50; */
  margin-top: 0px;
}


.badge {
  font-size: 50%;
}
.badge-danger {
  color: #fff;
  background-color: #c92411;
}

.navbar-nav {
  font-size: 1.2rem;
}
.navbar-light .navbar-nav .active>.nav-link {
  color: #c92411;
}
.navbar-light .navbar-nav .nav-link:hover {
  color: #c92411;
}
.navbar-light .navbar-nav .nav-link {
  color: #161d25;
}
.bg-light {
  background-color: #ffffff!important;
}
.navbar-light .navbar-toggler {
  border-color: transparent;
}


.form-control:focus {
  /* box-shadow: 0 0 0 1px rgb(202, 14, 14), 0 0 0 0.2rem rgb(255, 0, 64); */
  box-shadow: none;
  border-color: #c92411;
}

.form-control:active {
  color: #fff;
  background-color: #ffffff;
  border-color: #c92411;
}

.btn-outline-danger:hover {
    color: #fff;
    background-color: #c92411;
    border-color: #c92411
}

.navbar-brand {
  margin-right: 2.5rem;
}

button:focus {
  outline: none;
}



@media (max-width: 425px) {
  .bg-light {
    background-color: #ffffff!important;
  }
  .navbar-brand {
    margin-right: 0rem;
  }
}

.feather {
  width: 16px;
  height: 16px;
  stroke: #c92411;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
}

</style>



