<template>
  <div class="container" id="about">
		<div class="jumbotron jumbotron-fluid rounded shadow-sm px-2">
    <div class="container">
        <div class="row">
            <div class="col-sm-6">
              <div class="row my-2">
                <label>Ваше имя:</label>
                <input class="ml-2 rounded" type="name" id="name" placeholder="Имя Фамилия">
              </div>
            </div>
            <div class="col-sm-6">
              <div class="row my-2">
                <label>Ваш email:</label>
                <input class="ml-2 rounded" id="email" placeholder="mail@example.com">
              </div>
            </div>
        </div>
        <hr>
        <div class="row">
            <label>Ваше сообщение:</label>
            <textarea class="w-100 rounded" style="margin-top: 0px; margin-bottom: 0px; height: 180px;" placeholder="Вопросы, предложения, пожелания и тд."></textarea>
        </div>
        <div class="row mt-2">
            <button type="button" class="btn btn-outline-danger btn-lg btn-block">Отправить</button>
        </div>
    </div>
</div>
	</div>
</template>

<script>
export default {
  name: 'feedback'
}
</script>
<!-- styling for the component -->
<style>

#about {
  font-family: 'PT Sans Narrow', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #161d25;
  margin-top: 0rem;
}

.rounded {
  border-radius: 0.25rem;
}
/* .w-50 {
      width: 100%!important;
} */

label {
  margin-top: auto;
  margin-bottom: auto;
}

input {
  line-height: 1rem;
}
input, textarea {
  border-color: #999999;
  border-style: solid;
  border-width: 1px;
}

input:focus, textarea:focus {
  outline: none;
}

.btn-outline-danger {
  color: #c92411;
  border-color: #c92411;
}
.btn-outline-danger:hover {
  background-color: #c92411;
  border-color: #c92411;
}
.btn-outline-danger:focus {
  box-shadow: none;
  color: #c92411;
  border-color: #c92411;
  background-color: transparent;
}
.btn-lg {
  border-radius: .25rem;
}
</style>


